.card{
  position: relative;
  border: 1px solid #f3f3f3;
  padding: 10px;
  max-width: 175px;
  height: 350px;
  border-radius: 40px;
  margin-right: 30px;
  margin-bottom: 30px;

  transform: box-shadow 0.2s ease-in-out, transform 0.2s ease-in-out;
  
  cursor: pointer;

  &:hover{
    box-shadow: 0px 14px 35px rgba(0,0,0, 0.08);
    transform: translateY(-5px);
  }
  img{
    margin-top: 10px;
  }
}

.price{
  position:absolute;
  top: 250px;
  //right: 15px;
}

.title{
  width: 110px;
  margin-bottom: 20px;
}
.favorite{
  
  position: absolute;
  cursor: pointer;

  .button{
    border: none;
    border-radius: none;
  }
}

span{
  opacity: 0.5;
  font-size: 13px;
  text-transform: uppercase;
}

h5{
  font-weight: 400;
  font-size: 14px;
}

.plus_check{
  background: rgb(170, 255, 134);
  border: 1px solid #f2f2ff;
  border-radius: 8px;

  height: 32px;
  width: 32px;

  cursor: pointer;

  text-align: center;
}  

img{
    border-radius: 10px;
  }

.plus{

  cursor: pointer;
  height: 32px;
  width: 32px;

  margin-left: 15px;

  text-align: center;

  img{
    margin: 10px;
  }

  &:hover{
    background: lighten(hsl(87, 100%, 86%), 5%);
    border-radius: 5px;
  }

  &:active{
    background: darken(#9dd558, 5%);
    animation: submit 1s 0s;
  }

}

@keyframes submit {
  0%{
    background: darken(#9dd558, 5%);
  }
  100%{
    background: darken(#678d39, 5%);
  }
}